html{
    background-color: black;
}
body{
    margin: 0;
    padding: 0;
}

.background{
    background-color: black;
    background-size: cover;
    padding: 10px;
}

.form{
    padding: 10px;
    border-radius: 15px;
    background-color: white;
    width: 450px;
    z-index: 999;
    margin-left: auto;
    margin-right: auto;
}

.form h1{
    color: black;
    font-size: 40px;
    margin-top: 30px;
    padding-top: 10px;
    padding-bottom: 5px;
}

.form h3{
    color: grey;
    font-size: 15px;
    font-weight: 400;
    margin-bottom: 10px;
}

.form__content__right{
    display: grid;
    grid-template-columns: auto;
    background-color: black;
    width: 400px;
    margin-top: 10px;
    margin-bottom: 10px;
}

.form__inputs{
    margin: 10px;
    display: grid;
    grid-template-columns: auto;
    width: auto;
    margin-left: auto;
    margin-right: auto;
    justify-content: center;
}

.form__input__btn{
    color: white;
    background-color: black;
    border: none;
    padding: 5px 15px;
    border-radius: 10px;
    width: 300px;
    margin-top: 30px;
    margin-bottom: 20px;
}


.form__label p{
    font-weight: 700;
    margin-top: 15px;
    width: 400px;
}

.form__input{
    color: black;
    width: 400px;  
    padding: 11px;
    border: 1px solid #ccd0d5;
    border-radius: 10px;
}

.form__input::placeholder{
    color: #8c8f93;
}

.already__sign-in__div{
    margin-bottom: 80px;
    width: 400px;
    margin-left: auto;
    margin-right: auto;
}

.already__sign-in__div a{
    text-decoration: none;
}

.errors{
    color: red;
    margin-top: 20px;
}